@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 16px;
}

body,
.App {
    width: 100vw;
    height: auto;
    font-family: 'Karla', sans-serif;
}

@media only screen and (min-width: 1440px) {
    html {
        font-size: 20px;
    }
}
.different-container {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 3rem 0;
}

.line {
    width: 40%;
    text-align: center;
    border-top: 1px solid hsl(273, 4%, 51%);
    margin-top: 2rem;
}

h2 {
    font-family: 'DM Serif Display', serif;
    letter-spacing: 1.2px;
    padding: 2rem 0;
    font-size: 2rem;
}

.content {
    width: 90%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 1rem 0;
    text-align: center;
}

.content h4 {
    margin: 2rem 0;
}

.content img {
    max-width: 4rem;
}

.how-we-work {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-bottom: 2rem;
    margin: 3rem 0;
    text-align: center;
    color: hsl(0, 0%, 98%);
    background-color: hsl(270, 9%, 17%);
    padding: 3rem 0;
}

.view-plans-btn {
    margin-bottom: 2rem;
}

.work-desktop {
    display: none;
}

.work-mobile {
    position: absolute;
    top: 0;
    right: 0;
}

@media only screen and (min-width: 992px) {
    .different-container {
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: space-between;
        margin-top: 8rem;
        margin-bottom: 0;
        padding: 5rem;
    }
    .line {
        width: 10%;
        margin-left: 1rem;
        border-top: 1px solid hsl(273, 4%, 51%);
    }
    h2 {
        width: 100%;
        margin: 2rem 0;
    }
    .content {
        width: 30%;
        flex-wrap: wrap;
        margin: 1rem 0;
        text-align: left;
        align-items: flex-start;
    }
    .how-we-work {
        flex-direction: row;
        justify-content: space-between;
        padding: 0 2rem;
    }
    .how-we-work h2 {
        width: 40%;
        text-align: left;
    }
    .work-desktop {
        max-width: 80%;
        height: 100%;
        display: flex;
        position: absolute;
        top: 0;
        right: 0;
    }
    .work-mobile {
        display: none;
    }
}

@media only screen and (min-width: 1440px) {
    .how-we-work {
        padding: 4rem;
    }
}
.footer-container {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo-content {
    width: 80%;
    text-align: center;
    margin-top: 2rem;
    padding: 2rem 0;
    border-bottom: 1px solid lightgrey;
}

.logo,
.icon-content {
    padding-bottom: 1rem;
}

.icon-content img {
    margin: 0 1rem;
}

.text-content {
    margin-top: 2rem;
    text-transform: uppercase;
    text-align: center;
}

.text-content h5:nth-child(1) {
    color: grey;
    margin: 1rem 0;
    font-weight: 600;
}

.text-content h5 {
    padding: .3rem 0;
}

.attribution {
    margin-top: 2rem;
    text-align: center;
    font-size: .7rem;
}

.attribution a {
    color: hsl(228, 45%, 44%);
    text-decoration: none;
}

@media only screen and (min-width:992px) {
    .footer-container {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 0 5rem;
        margin: 0;
    }
    .logo-content {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border-bottom: none;
    }
    .text-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 20%;
        text-align: left;
    }
    .attribution {
        width: 100%;
        margin: 2rem 0;
        text-align: center;
        font-size: .7rem;
    }
}
.header {
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.navbar {
    width: 100vw;
    height: auto;
    font-family: 'DM Serif Display', serif;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem .5rem;
}

.nav-menu,
.hamburger.active,
.close,
.navbar-list {
    display: none;
}

.hamburger,
.close.active {
    display: flex;
    width: 10%;
    justify-content: flex-end;
}

.nav-menu.active {
    width: 100%;
    height: 70vh;
    background-color: hsl(270, 9%, 17%);
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-top: 2rem;
}

.nav-item {
    list-style-type: none;
    padding: 1rem 2rem;
}

li:nth-child(4) {
    border: 2px solid hsl(0, 0%, 98%);
}

.nav-item {
    text-align: center;
}

.nav-item a {
    color: hsl(0, 0%, 98%);
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1.2rem;
    letter-spacing: 1.1px;
}

.nav-pattern {
    position: absolute;
    bottom: 0;
    left: 0;
}

@media only screen and (min-width: 768px) {
    .nav-pattern {
        width: 100%;
        height: 40%;
    }
}

@media only screen and (min-width: 992px) {
    .navbar-list {
        width: 100vw;
        height: auto;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
    }
    .nav-item:nth-child(4) {
        border: 2px solid hsl(270, 9%, 17%);
    }
    .nav-item a {
        color: hsl(270, 9%, 17%);
    }
    .hamburger,
    .hamburger-icon,
    .close,
    .close-icon,
    .nav-menu,
    .nav-menu.active,
    .nav-pattern {
        display: none;
    }
}

@media only screen and (min-width: 1440px) {
    .navbar {
        padding: 1rem 2rem;
    }
}
.work-container {
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.desktop-bg-img,
.desktop-img,
.desktop-bg-right-img {
    display: none;
}

.img-container {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.text-container {
    width: 100%;
    min-height: 80vh;
    padding: 2rem;
    text-align: center;
    background-color: hsl(270, 9%, 17%);
    color: hsl(0, 0%, 98%);
    margin-top: -1rem;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.mobile-bg-img {
    position: absolute;
    left: 0;
    top: 0;
}

h1 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
}

.mobile-bg-right-img {
    position: absolute;
    right: 0;
    bottom: -11rem;
}

.view-plans-btn {
    max-width: 13rem;
    background-color: hsl(270, 9%, 17%);
    color: hsl(0, 0%, 98%);
    padding: 1rem 2rem;
    border: 1px solid hsl(0, 0%, 98%);
    margin-top: 2rem;
    text-transform: uppercase;
    letter-spacing: 1.1px;
    cursor: pointer;
}

.view-plans-btn:hover {
    background-color: hsl(0, 0%, 98%);
    color: hsl(270, 9%, 17%);
}

@media only screen and (min-width: 768px) {
    .text-container {
        padding-top: 6rem;
        margin: auto auto;
    }
}

@media only screen and (min-width: 992px) {
    .desktop-bg-right-img,
    .desktop-bg-img {
        display: flex;
        position: absolute;
    }
    .desktop-bg-img {
        width: 12%;
        bottom: -11rem;
        left: 0;
    }
    .desktop-bg-right-img {
        width: 35%;
        height: auto;
        top: -4rem;
        right: -2rem;
        z-index: 1;
    }
    .desktop-line {
        border-top: 1px solid hsl(0, 0%, 98%);
        width: 10%;
        margin-bottom: 2rem;
    }
    .text-container {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        padding-top: 2rem;
        text-align: left;
    }
    .text {
        display: flex;
        flex-direction: column;
        width: 40%;
        justify-content: flex-start;
        padding-top: 1rem;
    }
    .desktop-img {
        flex-grow: 1;
        display: flex;
        max-width: 50%;
        height: auto;
        z-index: 1;
        position: absolute;
        top: 10rem;
        right: 7rem;
    }
    .mobile-img,
    .mobile-bg-right-img,
    .mobile-bg-img {
        display: none;
    }
}

@media only screen and (min-width: 1440px) {
    .desktop-img {
        display: flex;
        top: 15rem;
        right: 10rem;
    }
}
